
/* header--mob GLOBAL styles  */

@media only screen and (max-width: 991px) {
  header.header--mob{
      display: flex;
  }
}


header.header--mob{
  background: #fff;
  height: 60px;
  flex-wrap: wrap;
  align-items: center;
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
}

.header--mob .row{
  align-items: center;
}

.header--mob .logo{
  max-width: 76px;
  width: 100%;
}

.header--mob .logo img{
  max-width: 100%;
}

.header--mob .phone--box{
  display: flex;
  width: 100%;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
}

.header--mob .phone--box a{
  font-family: var(--rb);
  font-size: 14px;
  line-height: 13px;
  color: #000000;
  width: 100%;
  text-align: center;
}

.header--mob .phone--box a + a{
  margin-top: 8px;
}

header.header--mob .cart-mob{
  position: relative;
  margin-right: 5px;
}

header.header--mob .cart--counter{
  width: 17px;
  height: 17px;
  border-radius: 100%;
  background-color: #fff;
  color: #000;
  position: absolute;
  top: -7px;
  right: -7px;
  box-shadow: 0px 0px 5px rgb(212, 212, 212);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: var(--rb);
  font-size: 9px;
}

/* search box mobile  */
.search--box{
  width: 100%;
  position: relative;
}

.search--box form{
  width: 100%;
  display: flex;
}

.search--box form.s-icon-left .submit{
  order: 1;
}

.search--box form.s-icon-left input{
  order: 2;
}

.search--box form.s-icon-right .submit{
  order: 2;
}

.search--box form.s-icon-right input{
  order: 1;
}

.search--box input{
  width: 100%;
  height: 26px;
  background: #2C2C2C;
  border: none;
  padding-left: 10px;
  padding-right: 10px;
  color: #fff;
  font-family: var(--rr)!important;
  font-size: 12px;
}

.search--box .submit {
  width: 60px;
  top: 0;
  font-size: 0;
  background: #4D4D4D;
  cursor: pointer;
  transition: 0.2s;
  padding-left: 17px;
  padding-right: 17px;
  border: none;
  outline: none;
  margin: 0;
  height: 26px;
  width: 31px;
  padding: 0;
  border-radius: 0px;
}

.search--box .submit.icon-left{
  margin-right: auto;
}

.search--box .submit img{
  max-width: 13px;
}


.search--box .submit:hover{
  background: #2b2b2b;
}

.search--box input::placeholder{
  color: #fff;
}

#search-result-mob {
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  background: #fff;
  box-shadow: 0px 4px 25px rgba(35, 31, 32, 0.07);
  display: none;
  margin-top: 5px;
  align-items: center;
  max-height: 70vh;
  overflow-x: auto;
  z-index: 999;
}

/* search result  */

#overlay_mob{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  display: none;
}

#overlay_mob.show{
  display: block;
}

#search-result-mob .s-item-link{
  display: flex;
  width: 100%;
  align-items: center;
  transition: 0.2s;
  padding: 10px 10px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.13);
}

#search-result-mob .s-item-link:last-child{
  border: none;
}

#search-result-mob .s-item-link:hover{
  background-color: rgba(0, 0, 0, 0.048);
}

#search-result-mob .s-item-img{
  width: 50px;
  margin-right: 10px;
}

#search-result-mob .s-item-img img{
  max-width: 100%;
  max-height: 50px;
}

#search-result-mob .s-item-info{
  width: 100%;
}

#search-result-mob .s-item-info span{
  margin: 2px 0;
}

#search-result-mob .s-item-info span:first-child{
  margin-top: 0;
}

#search-result-mob .s-item-info span:last-child{
  margin-bottom: 0;
}

#search-result-mob .s-item-title{
  font-family: var(--rr);
  display: inline-block;
  color: #000;
  width: 100%;
  font-size: 12px;
}

#search-result-mob .s-item-price{
  width: 100%;
  display: inline-block;
  font-family: var(--rb);
  color: #1C52AC;
}

#search-result-mob .all-items-link{
  position: sticky;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 30px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1C52AC;
  color: #fff;
  font-family: var(--rb);
  padding: 10px 10px;
  transition: 0.2s;
}

/* burger box  */
.burger-box{
  display: flex;
  width: 100%;
  height: 35px;
  align-items: center;
  margin-left: auto;
  justify-content: flex-end;
}

.burger-box .tel-mob{
  display: flex;
  align-items: center;
  font-family: var(--rb);
  font-size: 8px;
  line-height: 9px;
  color: #000000;
}

.burger-box .tel-mob img{
  margin-right: 7px;
  flex-shrink: 0;
}

.burger-box #my-button{
  margin: 0;
  width: auto;
  margin-left: 17px;
  flex-shrink: 0;
  padding-right: 0;
}

.burger-box .cart-mob img{
  width: 23px;
}



